
@charset "UTF-8";
.task-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.task-list__item, .task-list__item--checked {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    width: 270px;
    text-align: left;
}
.task-list__item--checked {
      color: #85a6c6;
}

/* transition-groupのname + "-item, -enter, -to,...のようなクラスが付与される"*/
.list-enter, .list-leave-to {
  opacity: 0;
  -webkit-transform: translate(2px);
          transform: translate(2px);
}
.list-leave-active {
  position: absolute;
}



